@use 'sass:color';

$background: #1a1a1a;

main {
    max-width: 800px !important;
}

a {
    text-decoration: none;
}

img {
    max-width: 500px;
}

.heading{
    font-size: 2em;
}

blockquote {
    position: relative;
    padding-left: 1em;
    border-left: 0.2em solid color.adjust($background, $lightness: 40%);
    font-size: inherit;
    line-height: inherit;
    font-weight: 100;
    font-style: italic;

    &:before, &:after {
        content: '\201C';
        color: color.adjust($background, $lightness: 40%)
    }
    
    &:after {
        content: '\201D';
    }
}

